// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/map-utils.scss';
@import '../tokens/style-tokens.scss';

@mixin eureka-focus-state-box-shadow($color: var(--eureka-color-universal-focus-halo, rgba(0, 0, 0, 0.4))) {
    box-shadow: 0 0 0 3px $color;
}

@mixin eureka-focus-state-halo($color: var(--eureka-color-universal-focus-halo, rgba(0, 0, 0, 0.4))) {
    transition: box-shadow 0.15s ease-in-out;

    &:focus {
        @include eureka-focus-state-reset;
    }

    &:focus-visible {
        @include eureka-focus-state-box-shadow($color);
    }
}

@mixin eureka-hover-state() {
    @media (hover: hover) {
        &:enabled:hover {
            cursor: pointer;
        }
    }
}

@mixin eureka-disabled-state($has-hover: true) {
    &:disabled {
        opacity: var(--eureka-core-state-disabled-opacity, eureka-get-map-value($eureka-style-tokens, 'core-state-disabled-opacity'));

        @if $has-hover {
            @media (hover: hover) {
                &:hover {
                    cursor: not-allowed;
                }
            }
        }
    }
}
